
.hero {
    width: 100%;
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: left;
    flex-direction: column;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

.headerContainer {
    width: auto;
    margin-left: 50px;
}

.headerContainer h1 {
    justify-content: center;
    align-items: center;
    font-size: 66px;
    height: 60px;
    font-weight: 50;
    color: #ffff;
    margin-bottom: 1.5rem;
}

.headerContainer p {
    padding: 0;
    font-size: 30px;
    font-weight: lighter;
    color: #ffff;
    margin-bottom: 1rem;
}

.vid{
    position: absolute;
    width: 100%;
    left: 50%;
    top: 50%;
    height: 100%;
    object-fit: cover;
    transform: translate(-50%,-50%);
    z-index: -1;
}


@media only screen and (max-width: 870px) {

    .home {
        justify-content: center;
        align-items: center;
    }
    
    
    .headerContainer {
        margin-left: 0px;
        border-radius: 10px;
        padding: 10px;
        width: 90%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        
        color: white;
    }
    
    .headerContainer h1 {
        font-size: 40px;
        height: 30%;
        color: white;
    }
    
    .headerContainer p {
        font-size: 30px;
        color: white;
    }
    
    .headerContainer button {
        margin-bottom: 30px;
    }
    
}

@media only screen and (max-width: 615px) {
    .home {
        justify-content: center;
        align-items: center;
    }
    
    
    .headerContainer {
        margin-left: 0px;
        border-radius: 10px;
        padding: 10px;
        width: 90%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        color: white;
    }
    
    .headerContainer h1 {
        font-size: 32px;
        height: 20%;
        color: white;
    }
    
    .headerContainer p {
        font-size: 20px;
        color: white;
    }
    
    .headerContainer button {
        margin-bottom: 30px;
    }
    

}