@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

body{
	line-height: 1.5;
	font-family: 'Poppins', sans-serif;
}
*{
	margin:0;
	padding:0;
	box-sizing: border-box;
}

.company-logo{
    color:#ffff;
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
	text-decoration: none;
	font-family: 'Montserrat',sans-serif;
}


.footercontainer{
	max-width: 1170px;
	margin:auto;
}
.footerrow{
	display: flex;
	flex-wrap: wrap;
}
ul{
	list-style: none;
}
.footer{
	background-color: #24262b;
    padding: 70px 0;
	clear: both;
    height: 100%;
	padding-top: 3em;
	position: relative;
	bottom: 0;
	width: 100%;
	margin-top: -10px;
}

.footer-left{
	position: relative;
	left: -80px;
}

.footer-col{
   width: 25%;
   padding: 0 15px;
}
.footer-col h4{
	font-size: 18px;
	color: #ffffff;
	text-transform: capitalize;
	margin-bottom: 35px;
	font-weight: 500;
	position: relative;
}

.footer-col ul li:not(:last-child){
	margin-bottom: 10px;
}
.footer-col ul li a{
	font-size: 16px;
	text-transform: capitalize;
	color: #ffffff;
	text-decoration: none;
	font-weight: 300;
	color: #bbbbbb;
	display: block;
	transition: all 0.3s ease;
}
.footer-col ul li a:hover{
	color: #ffffff;
	padding-left: 8px;
}
.footer-col .social-links a{
	display: inline-block;
	height: 40px;
	width: 40px;
	background-color: rgba(255,255,255,0.2);
	margin:0 10px 10px 0;
	text-align: center;
	line-height: 40px;
	border-radius: 50%;
	color: #ffffff;
	transition: all 0.5s ease;
}
.footer-col .social-links a:hover{
	color: #24262b;
	background-color: #ffffff;
}

.copyright{
	color: #AAAAAA;
	position: relative;
	bottom: -30px;
}

.arrow{
	position: relative;
	bottom: -40px;
	color: #AAAAAA;
}



/*responsive*/

@media(max-width:1500px){
	.footer-left{
		width: 100%;
		margin: 0px 0px 30px 150px;
	}
}

@media(max-width:900px){
	.footer-left{
		width: 100%;
		margin: 0px 0px 30px 150px;
	}
}

@media(max-width: 767px){
	.footer-left{
		width: 100%;
		margin: 0px 0px 30px 150px;
	}
	.footer-col{
	  width: 50%;
	  margin-bottom: 30px;
  }
  }
  @media(max-width: 574px){
	  .footer-left{
		  width: 100%;
		  margin: 0px 0px 30px 150px;
	  }
	.footer-col{
	  width: 100%;
  }
  }
  



